import React from 'react';
import { motion } from 'framer-motion';
import { FiChevronLeft, FiChevronRight } from 'react-icons/fi';
import { useLayout } from '../../hooks/useLayout';
import { ModelBadge } from './ModelBadge';
import { useSession } from '../../hooks/useSession';

/**
 * Navbar Component - IDE-inspired top navigation bar
 *
 * Design principles:
 * - Minimal design with focused functionality
 * - Clear visual separation from content area
 * - Adapts to available space with responsive layout
 * - Provides global context and navigation controls
 */
export const Navbar: React.FC = () => {
  const { isSidebarCollapsed, toggleSidebar } = useLayout();
  const { activeSessionId } = useSession();

  return (
    <div className="h-10 border-b border-gray-100/50 dark:border-gray-700/30 flex items-center px-2 backdrop-blur-sm">
      {/* Left section - Navigation controls */}
      <div className="flex items-center">
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
          onClick={toggleSidebar}
          className="p-1.5 text-gray-500 hover:text-gray-700 dark:hover:text-gray-300 hover:bg-gray-100/40 dark:hover:bg-gray-700/40 rounded-full transition-colors"
          title={isSidebarCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
        >
          {isSidebarCollapsed ? <FiChevronRight size={18} /> : <FiChevronLeft size={18} />}
        </motion.button>
      </div>

      {/* Center section - Context information */}
      <div className="flex-1 flex justify-center">{activeSessionId && <ModelBadge />}</div>

      {/* Right section - Action buttons */}
      <div className="flex items-center">{/* Future action buttons can be added here */}</div>
    </div>
  );
};
